import { BaseCommand } from './BaseCommand';
import { Command, XYZMoveMaterialsCommandData } from '../types/commands';
import { MoveCardCommand } from './MoveCardCommand';
import { Card, FieldZone, FieldZoneId } from '../types/types';
import { YGOUtils } from '../game/YGOUtils';
import { YGODuelEvents } from '../types/duel-events';
import { YGOCore } from '../game/YGOCore';
import { YGOGameUtils } from '../game/YGOGameUtils';

export class XYZMoveMaterialsCommand extends BaseCommand {
    public baseType: string = "XYZMoveMaterialsCommand";
    private data: XYZMoveMaterialsCommandData;
    private commands: Command[];
    private materialsToMove: { card: Card, zone: FieldZone }[];

    constructor(data: XYZMoveMaterialsCommandData) {
        super();
        this.type = "XYZ Move Material";
        this.data = data;
        this.commands = [];
        this.materialsToMove = [];
        console.log("EXEC MATERIALS");
    }

    init(ygo: YGOCore): void {
        super.init(ygo);
        const card = this.YGO.state.getCardById(this.data.id, this.data.originZone);

        // XYZ send materials to GY if send card to Gy or banish
        if (this.shouldMoveMaterials(card, this.data.originZone)) {
            const overlayZone = YGOUtils.getOverlayZone(this.data.originZone);
            this.materialsToMove = [];
            card.materials.forEach(material => {
                const zone = YGOGameUtils.createZone(this.data.zone!, material.originalOwner);
                this.YGO.state.setCard(material, zone);
                this.materialsToMove.push({ card: material, zone });
                console.log(" ...................... ");
                console.log("MOVE", material.name, material.originalOwner, " >> ZONE:", zone);
                if (this.data.zone === "GY") {
                    this.YGO.duelLog.dispatch<YGODuelEvents.SendToGY>({
                        player: this.data.player,
                        commandId: this.getCommandId(),
                        type: YGODuelEvents.LogType.SendToGY,
                        id: material.id,
                        originZone: overlayZone,
                        zone,
                        reason: "XYZ Material"
                    });
                }
            });
            console.log("MATERIALST OT MOVE", this.materialsToMove)
            card.materials = [];
        }
    }

    override exec(): void {
        this.execMultipleChildCommand(this.commands);
    }

    override undo(): void {
        this.undoMultipleChildCommand(this.commands);

        const card = this.YGO.state.getCardById(this.data.id, this.data.originZone);

        if (this.materialsToMove.length > 0) {
            this.materialsToMove.forEach(({ zone }) => {
                this.YGO.state.setCard(null, zone);
            });
        }

        card.materials = this.materialsToMove.map(({ card }) => card);
    }

    private shouldMoveMaterials(card: Card, zone: FieldZone): boolean {
        if (!card.materials || card.materials.length === 0) return false;
        return true;
    }
}

interface XYZMaterialsMoveData {
    player: number,
    id: number
    overlayZone: FieldZone
    zone: FieldZone
}

class XYZMaterialsMove extends BaseCommand {
    private data: XYZMaterialsMoveData;
    private card!: Card;

    constructor(data: XYZMaterialsMoveData) {
        super();
        this.data = data;
    }

    exec(): void {
        this.card = this.YGO.state.getCardById(this.data.id, this.data.zone);
        this.YGO.state.setCard(null, this.data.zone);

        this.YGO.duelLog.dispatch<YGODuelEvents.XYZOverlay>({
            player: this.data.player,
            commandId: this.getCommandId(),
            type: YGODuelEvents.LogType.XYZOverlay,
            id: this.data.id,
            originZone: this.data.zone,
            overlayZone: this.data.overlayZone
        });
    }

    undo(): void {
        this.YGO.state.setCard(this.card, this.data.zone);
    }
}
