import { BaseCommand } from './BaseCommand';
import { Command, TributeSummonCommandData } from '../types/commands';
import { MoveCardCommand } from './MoveCardCommand';
import { SendCardToGYCommand } from './SendCardToGY';
import { YGODuelEvents } from '../types/duel-events';
import { YGOCore } from '../game/YGOCore';
import { FieldZone } from '../types/types';

export class TributeSummonCommand extends BaseCommand {
    public baseType: string = "TributeSummonCommand";
    private data: TributeSummonCommandData;
    private tributes: { id: number, zone: FieldZone, owner: number }[] = [];
    private commands: Command[];

    constructor(data: TributeSummonCommandData) {
        super();
        this.data = data;
        this.data.position = this.data.position || "faceup-attack";
        this.type = this.data.position === 'faceup-attack' ? "Tribute Summon" : "Tribute Set";
        this.commands = [];


    }

    init(ygo: YGOCore): void {
        super.init(ygo);

        this.tributes = this.data.tributes.map(tribute => {
            const card = this.YGO.state.getCardById(tribute.id, tribute.zone);

            this.commands.push(new SendCardToGYCommand({
                id: tribute.id,
                originZone: tribute.zone,
                player: this.data.player,
                reason: "Tribute Summon"
            }))

            return {
                id: card.id,
                zone: tribute.zone!,
                owner: card.originalOwner
            }
        });

        this.commands.push(new MoveCardCommand({
            player: this.data.player,
            type: this.type,
            id: this.data.id,
            originZone: this.data.originZone,
            zone: this.data.zone,
            position: this.data.position,
            log: false
        }));

    }

    exec(): void {
        this.commands.forEach(cmd => this.execChildCommand(cmd));

        this.YGO.duelLog.dispatch<YGODuelEvents.TributeSummon>({
            player: this.data.player,
            commandId: this.getCommandId(),
            type: this.type === "Tribute Summon" ? YGODuelEvents.LogType.TributeSummon : YGODuelEvents.LogType.TributeSet,
            id: this.data.id,
            originZone: this.data.originZone,
            zone: this.data.zone,
            tributes: this.tributes,
            position: this.data.position!
        });
    }

    undo(): void {
        this.commands.forEach(cmd => this.undoChildCommand(cmd));
    }
}
