import { BaseCommand } from './BaseCommand';
import { XYZDetachCommandData } from '../types/commands';
import { YGOUtils } from '../game/YGOUtils';
import { Card } from '../types/types';
import { YGODuelEvents } from '../types/duel-events';
import { YGOGameUtils } from '../game/YGOGameUtils';

export class XYZDetachMaterialCommand extends BaseCommand {
    public baseType: string = "XYZDetachMaterialCommand";
    private data: XYZDetachCommandData;
    private materialCardReference!: Card;

    constructor(data: XYZDetachCommandData) {
        super();
        this.type = "XYZ Detach Material";
        this.data = data;
    }

    override exec(): void {
        const card = this.YGO.state.getCardById(this.data.id, this.data.originZone)!;
        this.materialCardReference = card.materials[this.data.materialIndex];
        card.materials.splice(this.data.materialIndex, 1);

        this.YGO.state.setCard(this.materialCardReference, YGOGameUtils.createZone("GY", this.materialCardReference.originalOwner));

        const overlayZone = YGOUtils.getOverlayZone(this.data.originZone);

        console.log(" DETACH ", overlayZone, ">>>", YGOGameUtils.createZone("GY", this.materialCardReference.originalOwner));

        this.YGO.duelLog.dispatch<YGODuelEvents.XYZDetach>({
            player: this.data.player,
            commandId: this.getCommandId(),
            type: YGODuelEvents.LogType.XYZDetachMaterial,
            id: card.id,
            materialIndex: this.data.materialIndex,
            materialId: this.materialCardReference.id,
            owner: this.materialCardReference.originalOwner,
            overlayZone: overlayZone
        });
    }

    override undo(): void {
        const card = this.YGO.state.getCardFromZone(this.data.originZone)!;
        card.materials.splice(this.data.materialIndex, 0, this.materialCardReference);
        console.log(" DETACH UNDO;;", YGOGameUtils.createZone("GY", this.materialCardReference.originalOwner));
        this.YGO.state.setCard(null, YGOGameUtils.createZone("GY", this.materialCardReference.originalOwner));
    }

}